/**
 * Claude Code version detection utility
 *
 * Runs `claude --version` to detect the installed Claude Code CLI version.
 * Result is cached to avoid repeated subprocess calls.
 */

import { execSync } from "node:child_process";
import { logger } from "./logger.js";

let cachedVersion: string | undefined;

/**
 * Get the installed Claude Code CLI version
 *
 * Runs `claude --version` with a timeout and caches the result.
 * Returns undefined if the command fails or times out.
 *
 * @returns Claude Code version string (e.g., "2.1.31") or undefined
 */
export function getClaudeCodeVersion(): string | undefined {
  if (cachedVersion !== undefined) {
    return cachedVersion;
  }

  try {
    const output = execSync("claude --version", {
      timeout: 2000,
      encoding: "utf-8",
      stdio: ["pipe", "pipe", "pipe"],
    });

    // Output format: "2.1.31 (Claude Code)"
    const version = output.trim().split(" ")[0];
    cachedVersion = version || undefined;

    logger.debug("Detected Claude Code version", { version: cachedVersion });
  } catch (error) {
    logger.debug("Could not detect Claude Code version", error);
    cachedVersion = undefined;
  }

  return cachedVersion;
}
